@import "../../../css-config/mixins.scss";

.product-card {
    width: calc(50% - 5px);
    margin-bottom: 20px;
    align-items: center;
    @include md {
        width: calc(25% - 15px);
    }

    .thumbnail {
        width: 100%;
        height: 180px;
        background-color: rgba(0, 0, 0, 0.5);
        margin-bottom: 10px;
        padding: 25px;
        display: flex;
        align-items: center;
        cursor: pointer;

        @include md {
            height: 350px;
        }

        img {
            transition: all ease 0.3s;
            display: block;
            width: 100%;
        }
       
    }
    .prod-details {
        .name {
            font-size: 14px;
            display: block;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            cursor: pointer;
            @include md {
                margin-bottom: 5px;
            }
       }
       .price {
        font-size: 18px;
        cursor: pointer;
       }

    }
    &:hover{
        .thumbnail {
            transform: scale(1.2);
        }
    }
}