.cart-products {
  flex-grow: 1;

  .cart-product {
    padding: 20px 15px;
    display: flex;
    gap: 10px;

    .img-container {
      background-color: rgba(0, 0, 0, 0.5);
      width: 60px;
      height: 60px;
      flex-shrink: 0;

      img {
        width: 100%;
        height: 100%;
      }
    }

    .prod-details {
      overflow: hidden;
      position: relative;

      .name {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        font-size: 16px;
        line-height: 1;
        margin-bottom: 10px;
        font-weight: 600;
        display: block;
        padding-right: 25px;
      }

      .text {
        display: flex;
        align-items: center;
        gap: 5px;
        font-size: 12px;
        font-weight: 600;
      }

      svg {
        position: absolute;
        top: 0;
        right: 0;
        cursor: pointer;
      }

      .noProducts {
        font-size: 30px;
        margin: 10px;
      }
    }
    &:hover {
      background-color: rgba(0, 0, 0, 0.2);
    }
  }
}
