@import "../../css-config/mixins.scss";

.cart-panel {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  z-index: 99;

  .opac-layer {
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .cart-content {
    width: 100%;
    height: 100%;
    display: flex;
    background-color: white;
    position: relative;
    z-index: 1;
    flex-direction: column;
    transform: translateX(100%);
    animation: slideCartWindow 0.3s ease forwards;

    @include md {
      width: 340px;
    }

    .cart-header {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding: 20px 15px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.5);

      .heading {
        flex-grow: 1;
        align-items: center;
        font-size: 20px;
        font-weight: 700;
        text-transform: uppercase;
      }

      .close-btn {
        display: flex;
        align-items: center;
        gap: 5px;
        cursor: pointer;

        svg {
          font-size: 18px;
        }

        .text {
          text-transform: uppercase;
          font-size: 14px;
        }

        &:hover {
          opacity: 0.5;
        }
      }
    }

    .cart-footer {
      border-top: 1px solid rgba(0, 0, 0, 0.5);

      .subtotal {
        padding: 20px 15px;
        display: flex;
        border-top: 1px solid rgba(0, 0, 0, 0.5);
        justify-content: space-between;

        .text {
          margin-bottom: 0;
          font-size: 20px;
          font-weight: 700;
          text-transform: uppercase;

          &.total {
            color: #000000;
          }
        }
      }
    }
    .button {
      padding: 20px 15px;

      .checkout-cta {
        outline: 0;
        border: 0;
        height: 50px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        font-size: 16px;
        color: white;
        background: #212121;
        border-bottom: 3px solid #4e4e4e;
      }
    }
  }
}

@keyframes slideCartWindow {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0);
  }
}
